.backgroundComponent {
    position: relative;
    width: 100%;
    min-height: 100vh;
    background-image: linear-gradient(to bottom, rgba(0, 0, 139, 0) 3800px, rgba(0, 0, 0, 0.12) 3900px, rgba(0, 0, 139, 0) 3900px), linear-gradient(to bottom, #e8e8e8 0%, #e8e8e8 3900px, darkblue 3900px, darkblue 100%);
    overflow: hidden;
    z-index: 1;
}

.circleH {
    width: 100px; /* Circle diameter */
    height: 100px; /* Circle diameter */
    border-radius: 50%; /* Makes it a circle */
    background-color: darkblue; /* Circle color */
    position: absolute;
    z-index:-1;
}

.animate {
    animation: slideIn 1s forwards;
}

@keyframes slideIn {
    to {
        transform: translateX(var(--slideDistance));
    }
}

@media (max-width: 1500px) {
    .circleH {
        width: 200px; /* Circle diameter */
        height: 200px; /* Circle diameter */
        border-radius: 50%; /* Makes it a circle */
        background-color: darkblue; /* Circle color */
        position: absolute;
        z-index: -1; 
    }
}
