/* RandomTipsTable.css */
.table-container {
    display: flex;
    overflow-x: auto;
    max-width: 100%;
    padding-top: 600px; /* Adjust based on your layout needs */
}

.table {
    width: 50%;
    min-width: 50px; /* Set a reasonable min-width for your table */
    border: solid 1px blue;
    margin: 20px;
}

    .table th {
        border-bottom: solid 3px red;
        background: aliceblue;
        color: black;
        font-weight: bold;
        cursor: pointer;
    }

    .table td {
        padding: 10px;
        border: solid 1px gray;
        background: papayawhip;
    }

/* Media query for screen sizes below 1200px */
@media (max-width: 1200px) {
    .table {
        width: 100%; /* Change the width to 100% for smaller screens */
    }

    .table-container {
        display: flex;
        overflow-x: auto;
        max-width: 100%;
        padding-top: 1050px; /* Adjust based on your layout needs */
    }
}
