html, body {
    height: 100%;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

#root, .flex-container {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}



.flex-container {
    width:100%;
    padding-top: 70px; /* Adjusted based on the larger header */
}







.footer {
    background-color: #2a3d66; /* Dark blue */
    color: white;
    text-align: center;
    padding: 4px 8px;
    position: fixed;
    height: 50px;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1000;
}


.footer-normal {
    position: static;
    bottom: auto;
}

.footer {
    /* Initial color */
    transition: background-color 1.5s ;
}

    .change-color-contact {
        /* New color for specific route */
        background-color: #36240c;
    }

.change-color-full-stack {
    /* New color for specific route */
    background-color: #967d0c;
}
.change-color {
    background-color: transparent; /* Replace with the color you want */
}





/* GAMES SUBHEADER */

.games-subheader {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-color: #3b6b7a; /* Dark greenish-blue */
    padding: 20px;
    padding-top: 100px; /* Increased top padding to account for the header */
    width: 150px;
    height: 100vh; /* Full viewport height */
    box-sizing: border-box;
    position: fixed;
    top: 0; /* Starts at the top */
    left: 0;
    z-index: 500;
    overflow:auto;
    animation: slideInFromLeft 0.5s ease-out forwards;
}

.game-link {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
    text-decoration: none;
    color: inherit;
    transition: transform 0.3s ease;

}

.g-subheader-minimize-b {
    position: absolute;
    left: 150px;
    top: 100px;
    width: 70px;
    height: 50px;
    background-color: black;
    border-radius: 5px;
    position: fixed;
    animation: slideInFromLeftPopUpSH 0.5s ease-out forwards;
}


.game-link:hover {
    transform: scale(1.05); /* Slightly enlarge the element */
}

.no-hover{
    pointer-events:none;
}


.game-link-clicked {
    transform: scale(0.8); /* Shrink to 80% of its size */
    transition: transform 0.2s ease;
}

.game-thumbnail {
    width: 100px;
    height: 100px;
    margin-bottom: 10px;
    border-radius: 50%; /* Makes the image round */
    box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.2); /* Slight shading */
}






/* GAME CONTAINER */



.game-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    box-sizing: border-box;
    margin-right: 20px;

}
.game-container {
    flex-direction: column;
    margin-left: 200px;
}

.game {
    width: 800px;
    height: 600px;
    margin-bottom: 20px;
}

.game-description {
    text-align: left;
    padding: 20px;
    max-width: 800px;
    margin: auto;
}







.interactive-particles {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1; /* Ensure it stays behind other content */
    background-color: lightgrey;
}










@keyframes slideInFromLeft {
    0% {
        transform: translateX(-100%);
    }

    100% {
        transform: translateX(0);
    }
}

@keyframes slideInFromLeftPopUpSH {
    0% {
        transform: translateX(-150px);
    }

    100% {
        transform: translateX(0px);
    }
}




@keyframes floatUp {
    0% {
        transform: translateY(100vh);
        opacity: 1;
    }


    100% {
        transform: translateY(-115vh);
        opacity: 1;
    }
}



.circle {
    position: fixed;
    bottom: 0;
    background-color: #2a3d66; 
    border-radius: 50%;
    opacity: 1; /* Remove fading effect */
    animation: floatUp 10s linear infinite;
    z-index: 10; /* Ensures circles stay behind most content */
}

.circle.small {
    width: 20px;
    height: 20px;
}

.circle.medium {
    width: 30px;
    height: 30px;
}

.circle.large {
    width: 50px;
    height: 50px;
}

circle.popped {
    animation: pop 0.3s forwards, shockwave 0.6s ease-out forwards;
    pointer-events: none; /* Prevents re-clicking while animating */
}

@keyframes pop {
    0% {
        transform: scale(1);
        opacity: 1;
    }

    100% {
        transform: scale(0);
        opacity: 0;
    }
}




.contactText {
    opacity: 1;

}


/* Fade-in effect */
.fade-in {
    animation: fadeInEffect forwards 1s;
}

@keyframes fadeInEffect {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}


@keyframes slideRight {
    0% {
        transform: translateX(0%);
    }

    100% {
        transform: translateX(-100%);
    }
}

@keyframes slideRightSH {
    0% {
        transform: translateX(0px);
    }

    100% {
        transform: translateX(-150px);
    }
}




.header-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 45px;
    background: #003946 !important;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    padding: 15px;
    z-index: 1003;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1); /* Ensures the header is on top */
}

.hamburger-menu {
    cursor: pointer;
    flex-direction: column;
    gap: 5px;
    display: none; /* Initially hidden, shown below the threshold */
    z-index: 1003; /* Makes sure hamburger is clickable and above the header */
}

    .hamburger-menu div {
        width: 30px;
        height: 3px;
        background-color: white;
    }

.nav-links {
    display: flex; /* Initially visible */
    gap: 3vw; /* Space between links */
    flex-direction: row; /* Align links in a row */
    align-items: center; /* Center links vertically */
    position: fixed;
    top: 60px; /* Below the header */
    right: 15px; /* Align to the right side of the screen */
    background: transparent; /* No background initially */
    width: auto; /* Width based on content */
    padding: 0;
    box-sizing: border-box;
    transform: translateY(-120%);
    transition: transform 0.5s ease-in-out;
    z-index: 10010; /* Below the header initially */
}

    .nav-links.show {
        transform: translateY(0);
        background: #003946; /* Background color for when nav-links are shown on small screens */
        width: 100%; /* Full width for small screens */
        padding: 20px 15px; /* Padding for small screens */
        flex-direction: column; /* Stack links vertically on small screens */
        align-items: flex-end; /* Align links to the right on small screens */
    }

.nav-item {
    color: white;
    text-decoration: none;
    font-size: 25px;
    font-weight: 600;
    transition: color 0.3s ease-in-out;
    position: relative; /* Necessary for after pseudo-element */
}

    .nav-item::after {
        content: '';
        display: block;
        width: 0;
        height: 3px;
        background: orange;
        transition: width 0.3s ease-in-out;
        position: absolute;
        bottom: -5px;
        left: 0;
    }

    .nav-item:hover {
        color: orange;
    }

        .nav-item:hover::after {
            width: 100%;
        }


    .header-title{
        color:white;
        font-size:34px;
        font-weight:600;
    }

@media (max-width: 1000px) {

    .header-title {
        color: white;
        font-size: 25px;
        font-weight: 500;
    }

    .nav-links {
        display: flex; /* Initially visible */
        gap: 2vw; /* Space between links */
        flex-direction: row; /* Align links in a row */
        align-items: center; /* Center links vertically */
        position: fixed;
        top: 60px; /* Below the header */
        right: 15px; /* Align to the right side of the screen */
        background: transparent; /* No background initially */
        width: auto; /* Width based on content */
        padding: 0;
        box-sizing: border-box;
        transform: translateY(-120%);
        transition: transform 0.5s ease-in-out;
        z-index: 10010; /* Below the header initially */
    }



    .nav-item {
        color: white;
        text-decoration: none;
        font-size: 22px;
        font-weight: 500;
        transition: color 0.3s ease-in-out;
        position: relative; /* Necessary for after pseudo-element */
    }
}


@media (max-width: 768px) {
    .nav-links {
        display: flex; /* Hide links initially */
        position: fixed; /* Ensures fixed positioning */
        top: 45px; /* Positioned right below the header */
        right: 0; /* Aligns to the right of the screen */
        left: 0; /* Stretches across the screen */
        width: 100%; /* Ensures it covers the full width */
        transform: translateY(-150%); /* Starts hidden above the viewport */
        z-index: 100; /* Positioned under the header z-index */
        background: #003946; /* Background color */
        flex-direction: column; /* Stack links vertically */
        align-items: flex-end; /* Align links to the left */
        padding: 20px 15px; /* Padding inside the nav-links container */
        box-sizing: border-box; /* Ensures padding doesn't affect overall width */
        transition: transform 0.5s ease-in-out; /* Smooth sliding effect */
    }

        .nav-links.show {
            display: flex; /* Shows the navigation links */
            transform: translateY(10%); /* Slides down into view */
        }

    .hamburger-menu {
        display: flex; /* Ensure the hamburger menu is visible */
    }
}

    @media (max-width: 700px) {
        .games-subheader {
            animation: slideRight forwards 1s;
            /* Increased top padding to account for the header */
        }

        .g-subheader-minimize-b {
            animation: slideRightSH forwards 1s;
        }


        .animSlideLeft {
            animation: slideInFromLeft forwards 1s;
        }

        .animSlideLeftSH {
            animation: slideInFromLeftPopUpSH forwards 1s;
        }
    }

