


.container-career-paragraph, .container-career-paragraphL {
    width: 30vw;
    text-align:justify;
}

.container-career-paragraphL {
    transform: translate(-100%, -100%);
}

.container-career-paragraph {
    transform: translateY( -100%);
}

.career-subtitle{
    display: flex;
    flex-direction: column;
    gap:30px;
}


.career-title{
    font-size:24px;
}

@media (max-width: 1000px) {
    .career-subtitle {
        font-size: 14px;
    }
    .career-title {
        font-size: 22px;
    }
}

    @media (max-width: 800px) {

        .container-career-paragraph, .container-career-paragraphL {
            width: 80vw;
            margin-left: -50px;
        }

        .container-career-paragraphL {
            transform: translateY( -100%);
        }

        .career-subtitle {
            font-size: 13px;
        }
        .career-title {
            font-size: 21px;
        }
    }

